<template>
  <transition name="move">
    <div v-show="showFlag" class="food" ref="food">
      <div class="content">
        <div class="image-header">
          <img :src="food.image" alt="">
        </div>
        <div class="close" @click="close"> ✘ </div>
        <div class="content-c">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="sell-count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}</span>
          </div>
          <div class="price">
            <span class="now">${{food.price}}</span>
            <del>
              <span v-show="food.oldPrice">${{food.oldPrice}}</span>
            </del>
          </div>
        </div>
        <div class="cartcontrol-wrapper">
          <cart-control :food="food" v-on:addCart="emit($event)"></cart-control>
        </div>
        <transition name="buy">
          <div class="buy" v-show="!food.count || food.count===0" @click="addFirst($event)">加入购物车</div>
        </transition>
        <split></split>
        <div class="introduction">
          <h1 class="title">商品介绍</h1>
          <div class="detail">{{food.info}}</div>
        </div>
        <split></split>
        <h1 class="title">商品评价</h1>
        <ratingselect :ratings="food.ratings" :select-type="selectType" :only-content="onlyContent" :desc="desc"
                      @rSelect="ratingtypeSelect" @cToggle="contentToggle"></ratingselect>
        <ul class="rating-wrapper" v-show="food.ratings && food.ratings.length">
          <li class="content" v-for="(rating, key) in food.ratings" :key="key+'rating'" v-show="needShow(rating.rateType,rating.text)">
            <div class="data">{{rating.rateTime}}</div>
            <div class="user">
              <span class="username">
                {{rating.username}}
              </span>
              <img :src=rating.avatar alt="" class="avatar">
            </div>
            <p class="detail">
              <i class="ratetype" :class="classMap[rating.rateType]">__</i>
              {{rating.text}}
            </p>
          </li>
        </ul>
      </div>
    </div>
  </transition>
</template>

<script>
import BScroll from 'better-scroll'
import cartcontrol from '../cartcontrol/cartcontrol.vue'
import split from '../split/spit.vue'
import ratingselect from '../ratingselect/retingselect.vue'
import Vue from 'vue'
export default{
  data () {
    return {
      showFlag: false,
      selectType: 2,
      onlyContent: false,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    }
  },
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    show () {
      // 私有的方法最好前面加个_
      this.showFlag = true
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          })
        }
      })
    },
    close () {
      this.showFlag = false
    },
    addFirst (event) {
      if (!event._constructed) {
        return
      }
      this.$emit('addCart', event.target)
      Vue.set(this.food, 'count', 1)
    },
    emit (event) {
      this.$emit('addCart', event)
    },
    needShow (type, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === 2) {
        return true
      } else {
        return type === this.selectType
      }
    },
    ratingtypeSelect (type) {
      this.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    contentToggle (onlyContent) {
      this.onlyContent = onlyContent
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    }
  },
  components: {
    'cart-control': cartcontrol,
    'split': split,
    'ratingselect': ratingselect
  },
  created () {
    this.classMap = ['goodrate', 'badrate']
    this.$nextTick(() => {
      this.ratings = this.food.ratings
    })
  },
  computed: {
    rateDate (time) {
      let data = new Date(time)
      return data.getFullYear() + data.getMonth()
    }
  }
}
</script>

<style scoped lang="sass">
  .food
    position: fixed
    left: 0
    top: 0
    bottom: 48px
    width: 100%
    z-index: 30
    background-color: #fff
    .content
      padding-bottom: 48px
      .close
        position: absolute
        top: 20px
        right: 20px
        font-size: 16px
        color: #ffffff
      .image-header
        position: relative
        width: 100%
        height: 0
        padding-top: 100%
        img
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
      .content-c
        padding: 18px
        .title
          line-height: 14px
          font-size: 14px
          font-weight: 700
          margin-bottom: 8px
          color: rgb(7, 17, 27)
        .detail
          margin-bottom: 5px
          line-height: 10px
          .sell-count, .rating
            font-size: 10px
            color: rgb(147, 153, 159)
          .sell-count
            margin-right: 12px
        .price
          font-weight: 700
          line-height: 24px
          color: rgb(147, 153, 159)
          >span
            font-size: 20px
            color: rgb(240, 20, 20)
            margin-right: 12px
          del:last-child
            font-size: 12px
          .now
            margin-right: 8px
            font-size: 14px
            color: rgb(240, 20, 20)
          .old
            text-decoration: line-through
            font-size: 10px
            color: rgb(147, 153, 159)
      .cartcontrol-wrapper
        position: absolute
        right: 12px
        top: 435px
      .buy
        position: absolute
        right: 18px
        top: 440px
        z-index: 10
        height: 24px
        line-height: 24px
        padding: 0 12px
        box-sizing: border-box
        border-radius: 12px
        font-size: 10px
        background-color: rgb(0, 160, 220)
        color: #ffffff
        &.buy-leave-active, &.but-enter-active
          transition: all .2s
        &.buy-leave-to, &.buy-enter
          opacity: 0
      .introduction
        margin: 18px
        .title
          font-weight: 700
          font-size: 16px
          margin-bottom: 6px
        .detail
          margin: 8px
          font-size: 12px
          font-weight: 200
          color: rgb(77, 85, 93)
          line-height: 24px
      .rating-wrapper
        margin: 0
        padding: 0
        .content
          list-style-type: none
          margin: 16px 18px
          border-bottom: 1px solid rgba(7, 17, 27, 0.1)
          padding: 0
          .data
            font-size: 10px
            color: rgb(147, 153, 159)
            line-height: 12px
            margin-bottom: 6px
          .user
            float: right
            .username
              font-size: 10px
              color: rgb(147, 153, 159)
              line-height: 12px
              padding-right: 6px
              vertical-align: top
            .avatar
              height: 12px
              width: 12px
              border-radius: 50%
          .detail
            font-size: 12px
            line-height: 16px
            color: rgb(7, 17, 27)
            .ratetype
              line-height: 24px
              margin-right: 4px
              color: rgb(147, 153, 159)
              &.goodrate
                background-color: #7698ff
              &.badrate
                background-color: #2b3e54
        &.move-enter-active, &.move-leave-active
          transition: all .2s linear
          transform: translate3d(0, 0, 0)
        &.move-enter, &.move-leave-to
          transform: translate3d(100%, 0, 0)
</style>



