  <div class="seller">
    {{seller}}
  </div>
</template>

<script>
import star from '@/components/star/star.vue'
import split from '../split/spit.vue'
export default{
  data () {
    return {}
  },
  props: {
    seller: {
      type: Object
    }
  },
  methods: {},
  components: {
    'star': star,
    'split': split
  },
  computed: {},
  created () {}
}
</script>

<style scoped lang="sass">
  .seller
    font-size: 12px
</style>



