import Vue from 'vue'
import Router from 'vue-router'
import goods from '@/components/goods/goods.vue'
import rating from '@/components/rating/rating.vue'
import seller from '@/components/seller/seller.vue'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/elm',
      name: 'goods',
      component: goods
    },
    {
      path: '/elm/seller',
      name: 'seller',
      component: seller
    },
    {
      path: '/elm/rating',
      name: 'rating',
      component: rating
    }
  ]
})



