<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(value, key) in goods" class="menu-item" :key="'menu'+key"
            :class="{'current': currentIndex===key}" @click="selectMenu(key, $event)">
          <span class="text border-1px">
          <span v-show="value.type>0" class="icon" :class="classMap[value.type]"></span>{{value.name}}</span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="(value, key) in goods" class="food-list food-list-hook" :key="'goods'+key">
          <h1 class="title">{{value.name}}</h1>
          <ul>
            <li @click="selectFood(food, $event)" v-for="(food, key) in value.foods"
                class="food-item" :key="'food'+key">
              <div class="icon">
                <img :src="food.icon" alt="">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span>月售{{food.sellCount}}份</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span>${{food.price}}</span>
                  <del>
                    <span v-show="food.oldPrice">${{food.oldPrice}}</span>
                  </del>
                </div>
                <div class="cartcontrol-wrapper">
                  <cart-control :food="food" v-on:addCart="_drop"></cart-control>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shop-cart ref="shopcart" :select-foods="selectFoods"
               :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shop-cart>
    <food :food="selectedFood" ref="food" v-on:addCart="_drop"></food>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import shopcart from '../shopcart/shopcart.vue'
import cartcontrol from '../cartcontrol/cartcontrol.vue'
import food from '../food/food.vue'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: '',
  data () {
    return {
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    }
  },
  props: {
    seller: {
      type: Object
    }
  },
  watch: {
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    this.getGoods()
    this.$nextTick(() => {
      this._initScroll()
      this._calculateHeight()
    })
  },
  computed: {
    currentIndex () {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i
        }
      }
      return 0
    },
    selectFoods () {
      let foods = []
      if (this.goods) {
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              // 一添加 这个值就会变化 从而引起这个属性重新计算
              foods.push(food)
            }
          })
        })
      }
      return foods
    },
    ...mapGetters([
      'goods'
    ])
  },
  methods: {
    _initScroll () {
      this.meunScroll = new BScroll(this.$refs.menuWrapper, {
        click: true
      })

      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
        click: true,
        probeType: 3
        // 实时返回滚动的位置
      })

      this.foodsScroll.on('scroll', (pos) => {
        // 监听
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    _calculateHeight () {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
    },
    selectMenu (index, event) {
      if (!event._constructed) {
        //  原生的事件没有该属性
        return 0
      }
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
      let rel = foodList[index]
      this.foodsScroll.scrollToElement(rel, 300)
    },
    _drop (target) {
      this.$refs.shopcart.drop(target)
    },
    selectFood (food, event) {
      if (!event._constructed) {
        return 0
      }
      this.selectedFood = food
      this.$refs.food.show()
    },
    ...mapActions([
      'getGoods'
    ])
  },
  components: {
    'shop-cart': shopcart,
    'cart-control': cartcontrol,
    'food': food
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
  @import "../../common/styles/mixin"
  .goods
    display: flex
    position: absolute
    top: 163px
    bottom: 46px
    width: 100%
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      ul
        padding-left: 0
      .menu-item
        display: table
        height: 54px
        width: 56px
        padding: 0 12px
        line-height: 14px
        &.current
          position: relative
          z-index: 10
          margin-top: -1px
          background: #fff
          font-weight: 700
        .icon
          display: inline-block
          vertical-align: top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease
            @include bg-image('decrease_3')
          &.discount
            @include bg-image('discount_3')
          &.guarantee
            @include bg-image('guarantee_3')
          &.invoice
            @include bg-image('invoice_3')
          &.special
            @include bg-image('special_3')
        .text
          display: table-cell
          width: 56px
          vertical-align: middle
          font-size: 12px
          border-bottom: 1px solid rgba(160, 160, 160, 0.55)
    .foods-wrapper
      flex: 1
      ul
        padding: 0
      .title
        margin: 0
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9dde1
        font-size: 12px
        color: rgb(147, 153, 159)
        background: #f3f5f7
      .food-item
        display: flex
        list-style-type: none
        margin: 18px
        padding-bottom: 18px
        &:last-child
          margin-bottom: 0
        .icon
          flex: 0 0 57px
          margin-right: 10px
        .content
          flex: 1
          position: relative
          .name
            margin: 2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size: 14px
            color: rgb(7, 17, 27)
          .desc, .extra
            line-height: 10px
            font-size: 10px
            color: rgb(147, 153, 159)
          .desc
            line-height: 12px
            margin-bottom: 8px
          .extra
            .count
              margin-right: 12px
          .price
            font-weight: 700
            line-height: 24px
            color: rgb(147, 153, 159)
            margin-top: 10px
            >span
              font-size: 20px
              color: rgb(240, 20, 20)
              margin-right: 12px
            del:last-child
              font-size: 12px
            .now
              margin-right: 8px
              font-size: 14px
              color: rgb(240, 20, 20)
            .old
              text-decoration: line-through
              font-size: 10px
              color: rgb(147, 153, 159)
          .cartcontrol-wrapper
            position: absolute
            right: 0
            bottom: 12px
</style>



