<template>
  <div class="cartcontrol" @click.stop>
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0" @click="delCart($event)">
        <img src="../../pages/index/assets/images/des.png" alt="">
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-increase" @click="addCart($event)">
      <img src="../../pages/index/assets/images/add.png" alt="">
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default{
  data () {
    return {
    }
  },
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    addCart (event) {
      if (!event._constructed) {
        return 0
      }
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1)
        this.food.count = 1
      } else {
        this.food.count++
      }
      this.$emit('addCart', event.target)
    },
    delCart (event) {
      if (!event._constructed) {
        return 0
      }
      if (this.food.count > 0) {
        this.food.count--
      }
    }
  },
  components: {},
  computed: {},
  created () {
  }
}
</script>

<style scoped lang="sass">
.cartcontrol
  font-size: 0
  .cart-decrease
    display: inline-block
    padding: 6px
    img
      height: 20px
      width: 20px
  .cart-count
    display: inline-block
    padding: 6px 0
    width: 24px
    font-size: 10px
    line-height: 20px
    color: rgb(147, 153, 159)
    vertical-align: top
    text-align: center
  .cart-increase
    display: inline-block
    padding: 6px
    img
      height: 20px
      width: 20px
.move-enter-active, .move-leave-active
  transition: opacity .5s
.move-leave-to, .move-enter
  opacity: 0
  transform: translate3d(24px, 0, 0)
</style>



