<template>
  <div class="ratingselect">
    <h1 class="title">评价</h1>
    <div class="ratetype">
      <span class="all" :class="{'active':$store.state.selectType===2}"  @click="$store.commit('selectType', 2)">{{desc.all}}
        <i class="num">{{ratings.length}}</i>
      </span>
      <span class="good" :class="{'active':$store.state.selectType===0}"  @click="$store.commit('selectType', 0)">{{desc.positive}}
          <i class="num">{{positives.length}}</i>
        </span>
      <span class="bad" :class="{'active':$store.state.selectType===1}"  @click="$store.commit('selectType', 1)">{{desc.negative}}
          <i class="num">{{negatives.length}}</i>
        </span>
    </div>
    <div class="content-filter">
      <span class="icon" @click="$store.commit('onlyContent')" :class="{'on':$store.state.onlyContent}">①</span>
      只看有内容的评价
    </div>
  </div>
</template>

<script>
const POSITIVE = 0
const NEGATIVE = 1
const ALL = 2
export default{
  data () {
    return {}
  },
  props: {
    ratings: {
      type: Array,
      default () {
        return []
      }
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default () {
        return {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      }
    }
  },
  methods: {
    select (type, event) {
      if (!event._constructed) {
        return
      }
      this.$emit('rSelect', type)
    },
    toggleContent (event) {
      if (!event._constructed) {
        return
      }
      this.onlyContent = !this.onlyContent
      this.$emit('cToggle', this.onlyContent)
    }
  },
  components: {},
  computed: {
    negatives () {
      return this.ratings.filter((rating) => rating.rateType === NEGATIVE)
    },
    positives () {
      return this.ratings.filter((rating) => rating.rateType === POSITIVE)
    }
  },
  created () {
  }
}
</script>

<style scoped lang="sass">
  .ratingselect
    margin: 18px
    .title
      font-size: 16px
      font-weight: 700
      margin-bottom: 6px
    .ratetype
      margin-top: 12px
      margin-bottom: 18px
      span
        display: inline-block
        padding: 8px 12px
        margin-right: 8px
        font-size: 12px
        line-height: 16px
        border-radius: 1px
        background-color: rgba(0, 160, 220, 0.2)
        .num
          font-size: 8px
          font-weight: 200
        &.bad
          background-color: rgba(77, 85, 93, 0.2)
        &.active
          background-color: rgb(0, 160, 220)
          color: rgb(255, 255, 255)
    .content-filter
      padding: 12px 0
      border-top: 1px solid rgba(7, 17, 27, 0.1)
      border-bottom: 1px solid rgba(7, 17, 27, 0.1)
      font-size: 12px
      color: rgb(147, 153, 159)
      line-height: 24px
      .icon
        margin-left: 4px
        background-color: rgba(77, 85, 93, 0.2)
        &.on
          background-color: #7698ff
</style>



