<template>
  <div class="reset">
    <header>
      <img :src=seller.avatar alt="" width="64" height="64" class="logo">
      <section class="content-wrapper">
          <div class="title">
            <span class="brand"><img src="brand@2x.png" alt=""></span>
            <span class="name">{{ seller.name }}</span>
          </div>
          <div class="description">
            {{ seller.description}}/{{seller.deliveryTime}}分钟送达
          </div>
          <div v-if="seller.supports" class="support">
            <span class="icon" :class=classMap[seller.supports[0].type]></span>
            <span class="text">{{seller.supports[0].description}}</span>
          </div>
        <div class="support-count" v-if="seller.supports" @click="showDetail">
          <span class="count"> {{ seller.supports.length}}个</span>
          <i> > </i>
        </div>
      </section>
      <div class="bulletin-wrapper" @click="showDetail">
        <span class="bulletin-tittle"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i> > </i>
      </div>
    </header>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <div v-show="detailShow" class="detail" @click="closeW">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wrapper">
            <star :score="seller.score" :size="48"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item" v-for="item in seller.supports" :key="item.index">
              <span class="item-icon" :class=classMap[item.type]></span>
              <span class="text"> {{item.description}}</span>
            </li>
          </ul>
          <div class="title">
            <div class="line"></div>
            <div class="text">商家活动</div>
            <div class="line"></div>
          </div>
          <div class="supports-bulletin">
            {{seller.bulletin}}
          </div>
        </div>
      </div>
      <div class="detail-close"> 关 </div>
    </div>
  </div>
</template>

<script>
import star from '@/components/star/star.vue'
export default {
  name: '',
  data () {
    return {
      detailShow: false
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  },
  props: {
    seller: {
      type: Object
    }
  },
  methods: {
    showDetail () {
      this.detailShow = true
    },
    closeW () {
      this.detailShow = false
    }
  },
  components: {
    'star': star
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
  @import "../../common/styles/mixin"
  @import "../../common/styles/base"
  .reset
    position: relative
    overflow: hidden
  header
    color: #fff
    /*background-color: #a0a0a0*/
    background-color: rgba(7,17,27,0.5)
    padding-top: 12px
    .logo
      vertical-align: top
      display: inline-block
      border-radius: 4px
      margin-left: 12px
      margin-bottom: 18px
    .content-wrapper
      display: inline-block
      margin-left: 15px
      font-size: 14px
      .title
        margin: 2px 0 8px 0
        .brand
          display: inline-block
          width: 30px
          height: 18px
          vertical-align: top
          @include bg-image('brand')
          background-size: 30px 18px
          background-repeat: no-repeat
        .name
          margin-left: 6px
          font-size: 16px
          line-height: 18px
          font-weight: bold
      .description
        margin-bottom: 5px
        line-height: 12px
        font-size: 12px
      .support
        .icon
          display: inline-block
          vertical-align: middle
          width: 12px
          height: 12px
          margin-right: 4px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease
            @include bg-image('decrease_1')
          &.discount
            @include bg-image('discount_1')
          &.guarantee
            @include bg-image('guarantee_1')
          &.invoice
            @include bg-image('invoice_1')
          &.special
            @include bg-image('special_1')
        .text
          font-size: 10px
          line-height: 12px
    .support-count
      position: absolute
      right: 12px
      bottom: 50px
      padding: 0 8px
      height: 24px
      line-height: 24px
      border-radius: 14px
      background-color: rgba(0, 0, 0, 0.2)
      text-align: center
      .count
        font-size: 10px
    .bulletin-wrapper
      height: 28px
      line-height: 28px
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsis
      font-size: 12px
      padding: 0 12px
      background-color: rgba(7,17,27,0.2)
      position: relative
      .bulletin-tittle
        display: inline-block
        width: 22px
        height: 12px
        vertical-align: top
        margin-top: 8px
        @include bg-image('bulletin')
        background-size: 22px 12px
      .bulletin-text
        margin: 0 24px 0 4px
      i
        width: 10px
        position: absolute
        right: 8px
  .background
    z-index: -1
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    filter: blur(10px)
  .detail
    position: fixed
    z-index: 100
    width: 100%
    height: 100%
    overflow: auto
    background-color: rgba(7, 17, 27, 0.8)
    top: 0
    color: #ffffff
    font-size: 12px
    .detail-wrapper
      width: 100%
      min-height: 100%
      .detail-main
        margin-top: 64px
        padding-bottom: 64px
        .name
          line-height: 16px
          text-align: center
          font-size: 16px
          font-weight: 700
        .star-wrapper
          margin-top: 18px
          text-align: center
          padding: 2px 0
        .title
          width: 80%
          margin: 30px auto
          display: flex
          .line
            flex: 1
            position: relative
            top: -10px
            border-bottom: 2px solid rgba(155, 255, 255, 0.2)
          .text
            padding: 0 12px
            font-size: 14px
        .supports
          padding: 0 36px
          margin-bottom: 28px
          .support-item
            height: 16px
            list-style-type: none
            margin-bottom: 12px
            padding: 0 12px
            .item-icon
              display: inline-block
              width: 16px
              height: 16px
              margin-right: 6px
              background-size: 16px 16px
              background-repeat: no-repeat
              vertical-align: top
              &.decrease
                @include bg-image('decrease_2')
              &.discount
                @include bg-image('discount_2')
              &.guarantee
                @include bg-image('guarantee_2')
              &.invoice
                @include bg-image('invoice_2')
              &.special
                @include bg-image('special_2')
            .text
              display: inline-block
              font-size: 12px
              font-weight: 200
              color: rgba(255, 255, 255, 1)
              line-height: 12px
        .supports-bulletin
          padding: 0 48px
          font-size: 12px
          line-height: 24px
          font-weight: 200
    .detail-close
      position: relative
      width: 32px
      height: 32px
      margin: -64px auto 0 auto
      clear: both
      font-size: 32px

</style>



