<template>
  <div class="shopcart">
    <div class="content">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'highlight': totalCount>0}"  @click.self="toggleList">
            <span class="icon-shopping_cart" :class="{'highlight': totalCount>0}">
            </span>
          </div>
          <div class="num">{{ totalCount }}</div>
        </div>
        <div class="price" :class="{'highlight': totalCount>0}">$ {{totalPrice}} </div>
        <div class="desc" @click.self="toggleList">另需配送费 $  {{deliveryPrice}} 元</div>
      </div>
      <div class="content-right">
        <div class="pay" :class="payClass" @click="payment($event)">
            {{ payDesc }}
          <!--全部文字由计算属性计算-->
        </div>
      </div>
    </div>
    <div class="ball-container">
      <transition-group name="drop"
                        v-on:before-enter="beforeEnter"
                        v-on:enter="enterr"
                        v-on:after-enter="afterEnter">
         <div v-for="(ball, key) in balls" v-show="ball.show" :key="key" class="ball">
           <div class="inner inner-hook">加</div>
         </div>
      </transition-group>
    </div>
    <transition name="fold">
      <div class="shop-list" v-show="listShow">
        <div class="list-header">
          <h1 class="title">购物车</h1>
          <span class="empty" @click="emptyCart">清空</span>
        </div>
        <div class="list-content" ref="cartWrapper">
          <ul>
            <li v-for="(food, key) in selectFoods" class="food" :key="key">
              <span class="name">{{ food.name}}</span>
              <div class="price">
                <span>${{food.price * food.count}}</span>
              </div>
              <div class="cartcontrol-wrapper">
                <cart-control :food="food"></cart-control>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import cartcontrol from '../cartcontrol/cartcontrol.vue'
export default{
  data () {
    return {
      balls: [
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        }
      ],
      dropBalls: [],
      fold: true
    }
  },

  methods: {
    drop (el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i]
        if (!ball.show) {
          ball.show = true
          ball.el = el
          this.dropBalls.push(ball)
          return
        }
      }
    },
    toggleList () {
      if (!this.totalCount) {
        return
      }
      this.fold = !this.fold
    },
    hideList () {
      this.fold = true
    },
    empty (event) {
      this.selectFoods.forEach((food) => {
        food.count = 0
      })
    },
    pay () {
      if (this.totalPrice < this.minPrice) {
        return
      }
      window.alert(`支付${this.totalPrice}元`)
    },
    beforeEnter (el) {
      let count = this.balls.length
      while (count--) {
        let ball = this.balls[count]
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect()
          let x = rect.left - 32
          let y = -(window.innerHeight - rect.top - 22)
          el.style.display = 'block'
          el.style.webkitTransform = `translate3d(0,${y}px,0)`
          el.style.transform = `translate3d(0,${y}px,0)`
          let inner = el.getElementsByClassName('inner-hook')[0]
          inner.style.webkitTransform = `translate3d(${x}px,0,0)`
          inner.style.transform = `translate3d(${x}px,0,0)`
        }
      }
    },
    enterr (el) {
      this.$nextTick(() => {
        el.style.webkitTransform = 'translate3d(0,0,0)'
        el.style.transform = 'translate3d(0,0,0)'
        let inner = el.getElementsByClassName('inner-hook')[0]
        inner.style.webkitTransform = 'translate3d(0,0,0)'
        inner.style.transform = 'translate3d(0,0,0)'
      })
    },
    afterEnter (el) {
      let ball = this.dropBalls.shift()
      if (ball) {
        ball.show = false
        el.style.display = 'none'
      }
    },
    _initScroll () {
      this.cartScroll = new BScroll(this.$refs.cartWrapper, {
        click: true
      })
    },
    emptyCart () {
      this.selectFoods.forEach((food) => {
        food.count = 0
      })
    },
    payment (event) {
      if (event.target.innerHTML.trim() === '去结算') {
        this.$store.commit('payment', this.selectFoods)
      }
    }
  },
  props: {
    selectFoods: {
      type: Array,
      default () {
        return [
          {
            price: 0,
            count: 1
          }
        ]
      }
    },
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    }
  },
  computed: {
    totalPrice () {
      let total = 0
      this.selectFoods.forEach((food) => {
        total += food.price * food.count
      })
      return total
    },
    totalCount () {
      let count = 0
      this.selectFoods.forEach((food) => {
        count += food.count
      })
      return count
    },
    payDesc () {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}元起送`
      } else if (this.totalPrice < this.minPrice) {
        let diff = this.minPrice - this.totalPrice
        return `还差￥${diff}元起送`
      } else {
        return '去结算'
      }
    },
    payClass () {
      if (this.totalPrice < this.minPrice) {
        return 'not-enough'
      } else {
        return 'enough'
      }
    },
    listShow () {
      if (!this.totalCount) {
        return false
      }
      let show = !this.fold
      if (show) {
        this.$nextTick(() => {
          this._initScroll()
        })
      }
      return show
    }
  },
  created () {
    this.$nextTick(() => {
    })
  },
  components: {
    'cart-control': cartcontrol
  }
}
</script>

<style scoped lang="sass">
  ul, li
    margin: 0
    padding: 0
    list-style-type: none
  .shopcart
    position: fixed
    left: 0
    bottom: 0
    z-index: 50
    width: 100%
    height: 48px
    .content
      display: flex
      background-color: #141d27
      font-size: 0
      .content-left
        flex: 1
        .logo-wrapper
          display: inline-block
          position: relative
          top: -20px
          margin: 0 12px
          padding: 6px
          width: 56px
          height: 56px
          background-color: #141d27
          border-radius: 50%
          .logo
            width: 100%
            height: 100%
            text-align: center
            border-radius: 50%
            background-color: rgb(43, 62, 84)
            .icon-shopping_cart
              display: inline-block
              font-size: 24px
              line-height: 44px
              color: #a0a0a0
            &.highlight
              background-color: #7698ff
          .num
            position: absolute
            top: 0
            right: 0
            width: 24px
            height: 16px
            line-height: 16px
            text-align: center
            border-radius: 16px
            font-size: 9px
            font-weight: 700
            color: #ffffff
            background-color: rgb(240, 20, 20)
            border-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4px)
        .price
          display: inline-block
          vertical-align: top
          line-height: 24px
          box-sizing: border-box
          margin-top: 12px
          padding-right: 12px
          border-right: 1px solid rgba(255, 255, 255, 0.1)
          font-size: 16px
          font-weight: 700
          color: #80858a
          &.highlight
            color: #ffffff
        .desc
          display: inline-block
          font-size: 10px
          color: #80858a
          vertical-align: top
          margin: 12px 0 0 12px
          line-height: 24px
      .content-right
        flex: 0 0 105px
        width: 105px
        background-color: #334963
        .pay
          font-size: 12px
          line-height: 48px
          height: 48px
          font-weight: 700
          text-align: center
          color: #a0a0a0
          background: #2b333b
          &.not-enough
            background: #2b333b
          &.enough
            background-color: #74c546
            color: #ffffff
    .ball-container
      .ball
        position: fixed
        left: 32px
        bottom: 22px
        z-index: 200
        &.drop-enter-active, .drop-leave-active
          transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41)
          .inner
            width: 16px
            height: 16px
            border-radius: 50%
            background: rgb(0, 160, 220)
            transition: all 0.4s linear
    .shop-list
      position: absolute
      left: 0
      bottom: 48px
      z-index: -1
      width: 100%
      font-size: 0
      &.fold-enter-active, &.fold-leave-active
        transition: all .5s
        transform: translate3d(0, 0, 0)
      &.fold-enter, &.fold-leave-to
        transform: translate3d(0, 100%, 0)
      .list-header
        height: 40px
        padding: 0 18px
        background: #f3f5f7
        vertical-align: center
        .title
          float: left
          font-size: 14px
          color: rgb(7, 17, 27)
        .empty
          float: right
          font-size: 12px
          color: rgb(0, 160, 220)
          line-height: 40px
      .list-content
        padding: 0 18px
        max-height: 217px
        overflow: hidden
        background: #fff
        width: 100%
        .food
          position: relative
          padding: 12px 0
          box-sizing: border-box
          .name
            line-height: 24px
            font-size: 14px
            color: rgb(7, 17, 27)
          .price
            position: absolute
            right: 120px
            bottom: 12px
            line-height: 24px
            font-size: 14px
            font-weight: 700
            color: rgb(240, 20, 20)
          .cartcontrol-wrapper
            position: absolute
            right: 20px
            bottom: 6px

</style>



