<template>
    <div id="app">
      <e-header :seller="seller"></e-header>
      <nav>
        <router-link :to="{ name: 'goods', params: { userId: 123 }}">商品</router-link>
        <router-link :to="{ name: 'rating', params: { userId: 123 }}">评论</router-link>
        <router-link :to="{ name: 'seller', params: { userId: 123 }}">商家</router-link>
      </nav>
      <router-view :seller="seller" class="view"></router-view>
    </div>
</template>

<script>
import header from '../../components/header/header.vue'
import goods from '@/components/goods/goods.vue'
import rating from '@/components/rating/rating.vue'
export default {
  name: '',
  data () {
    return {
    }
  },
  components: {
    'e-header': header,
    'e-goods': goods,
    'e-rating': rating
  },
  created () {
    this.$store.dispatch('getSeller')
  },
  computed: {
    seller () {
      return this.$store.state.seller
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
  nav
    display: flex
    flex-wrap: nowrap
    background-color: #ffffff
    height: 40px
    border-bottom: 1px solid #2d374b
    *
      width: 33%
      font-size: 14px
      line-height: 40px
      text-align: center
      vertical-align: middle
      color: rgba(77, 85, 93, 1)
  .view
    width: 100%
</style>



