import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

const state = {
  selectType: 2,
  onlyContent: true,
  seller: {},
  goods: [],
  ratings: [],
  shopCart: {}
}

const mutations = {
  selectType (state, type) {
    state.selectType = type
  },
  onlyContent (state) {
    state.onlyContent = !state.onlyContent
  },
  getSeller (state, data) {
    state.seller = data
  },
  getGoods (state, data) {
    state.goods = data
  },
  getRatings (state, data) {
    state.ratings = data
  },
  payment (state, selectFoods) {
    state.shopCart = selectFoods
    axios.post('/payment', selectFoods).then(() => {
      console.log('结算成功')
    }).catch(() => {
      console.log('结算失败')
    })
  }
}

const actions = {
  async getSeller (context) {
    axios.get('/api/seller').then((res) => {
      if (res.status === 200) {
        context.commit('getSeller', res.data.data)
      }
    })
  },
  async getGoods (context) {
    axios.get('/api/goods').then((res) => {
      if (res.status === 200) {
        context.commit('getGoods', res.data.data)
      }
    })
  },
  async getRatings (context) {
    axios.get('/api/ratings').then((res) => {
      if (res.status === 200) {
        context.commit('getRatings', res.data.data)
      }
    })
  }
}

const getters = {
  goods: state => state.goods,
  ratings: state => state.ratings,
  seller: state => state.seller,
  shopCart: state => state.shopCart
}

export default new Vuex.Store({
  state, mutations, actions, getters
})



