<template>
  <div class="rating">
    <div class="seller-rate">
      <div class="rate-left">
        <p class="score">{{seller.score}}</p>
        <p class="rate">综合评分</p>
        <p class="rank">高于周边商家{{seller.rankRate}}%</p>
      </div>
      <div class="rate-right">
        <div class="foodscore">
          <span>食物评分</span>
          <star :score="seller.foodScore" :size="36"></star>
          <span>{{seller.foodScore}}</span>
        </div>
        <div class="servicescore">
          <span>服务评分</span>
          <star :score="seller.serviceScore" :size="36" class="star"></star>
          <span>{{seller.serviceScore}}</span>
        </div>
        <div class="deliverytime">
          <span>送达时间</span>
          <span class="time">{{seller.deliveryTime}}分钟</span>
        </div>
      </div>
    </div>
    <split></split>
    <ratingselect :ratings="ratings" :select-type="selectType" :only-content="onlyContent"
                  :desc="desc"></ratingselect>
    <ul class="rating-wrapper">
      <li class="rating-item" v-for="(ratingDetail, key) in ratings" :key="key+'kk'"
      v-show="needShow(ratingDetail.rateType, ratingDetail.text)">
        <p>{{ratingDetail.text}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import star from '@/components/star/star.vue'
import split from '../split/spit.vue'
import ratingselect from '../ratingselectt/retingselect.vue'
import { mapActions, mapGetters } from 'vuex'
export default{
  data () {
    return {
      selectType: 2,
      onlyContent: false,
      desc: {
        all: '全部',
        positive: '满意',
        negative: '不满意'
      }
    }
  },
  props: {
    seller: {
      type: Object
    }
  },
  methods: {
    ...mapActions([
      'getRatings'
    ]),
    needShow (type, text) {
      if (this.$store.state.onlyContent && !text) {
        return false
      }
      if (this.$store.state.selectType === 2) {
        return true
      } else {
        return type === this.$store.state.selectType
      }
    }
  },
  components: {
    'star': star,
    'split': split,
    'ratingselect': ratingselect
  },
  computed: {
    ...mapGetters([
      'ratings'
    ])
  },
  created () {
    this.getRatings()
  }
}
</script>

<style scoped lang="sass">
  .rating
    .seller-rate
      padding: 18px 0
      display: flex
      .rate-left
        flex: 0 0 137px
        border-right: 1px solid rgba(45, 55, 75, 0.59)
        width: 137.5px
        text-align: center
        .score
          font-size: 24px
          line-height: 28px
          color: rgb(255, 153, 0)
          margin-bottom: 6px
          margin-top: 15px
        .rate
          font-size: 12px
          color: rgb(7, 17, 27)
          line-height: 12px
          margin-bottom: 8px
        .rank
          font-size: 10px
          color: rgba(7, 17, 27, 0.5)
          line-height: 10px
          margin-bottom: 6px
      .rate-right
        flex: 1
        padding: 0 24px
        font-size: 0
        color: rgb(7, 17, 27)
        line-height: 18px
        div
          span
            font-size: 12px
          .star
            margin: 0 12px
            display: inline-block
          span:nth-child(3)
            font-size: 12px
            color: rgb(255, 165, 97)
        &:nth-child(2)
          margin: 8px 0
        .servicescore
          margin: 8px 0
        .deliverytime
          .time
            margin-left: 12px
            font-size: 12px
            color: rgb(147, 153, 159)
    .rating-wrapper
      font-size: 12px
      .rating-item
        list-style-type: armenian
</style>



